import * as React from 'react';
import { FileFieldProps } from 'react-admin';
import { FC, MouseEvent, useCallback, memo } from 'react';
import { s3FileFieldType } from '../types';
import { makeStyles } from '@material-ui/core/styles';
import classnames from 'classnames';
import { useRecordContext } from 'ra-core';
import { sanitizeFieldRestProps } from '../utils';
import Typography from '@material-ui/core/Typography';
import _get from 'lodash/get';
import _isFunction from 'lodash/isFunction';

const S3FileField: FC<S3FileFieldProps> = memo<S3FileFieldProps>(props => {
  const {
    className,
    classes: classesOverride,
    emptyText,
    source = '',
    title = '',
    src = '',
    target,
    download,
    ping,
    rel,
    onClick,
    ...rest
  } = props;
  const record = useRecordContext(props);
  const sourceValue = _get(record, source) || _get(record, title);

  const classes = useStyles(props);

  const handleClick = useCallback(
    event => {
      event.preventDefault();
      if (_isFunction(onClick)) {
        onClick(event);
      }
    },
    [onClick]
  );

  if (!sourceValue) {
    return emptyText ? (
      <Typography
        component="span"
        variant="body2"
        className={className}
        {...sanitizeFieldRestProps(rest)}
      >
        {emptyText}
      </Typography>
    ) : (
      <div
        className={classnames(classes.root, className)}
        {...sanitizeFieldRestProps(rest)}
      />
    );
  }

  if (Array.isArray(sourceValue)) {
    return (
      <ul
        className={classnames(classes.root, className)}
        {...sanitizeFieldRestProps(rest)}
      >
        {sourceValue.map((file, index) => {
          const fileTitleValue = _get(file, title) || title;
          const srcValue = _get(file, src) || title;
          const key = _get(file, 'key', '');

          return (
            <li key={index}>
              <a
                href={srcValue}
                title={fileTitleValue}
                target={target}
                download={download}
                ping={ping}
                rel={rel}
                data-key={key}
                onClick={handleClick}
              >
                {fileTitleValue}
              </a>
            </li>
          );
        })}
      </ul>
    );
  }

  const titleValue = _get(record, title) || title;
  const key = _get(record, 'key', '');
  return (
    <div
      className={classnames(classes.root, className)}
      {...sanitizeFieldRestProps(rest)}
    >
      <a
        href={sourceValue}
        title={titleValue}
        target={target}
        download={download}
        ping={ping}
        rel={rel}
        onClick={handleClick}
        data-key={key}
      >
        {titleValue}
      </a>
    </div>
  );
});

S3FileField.displayName = 'S3FileField';

const useStyles = makeStyles(
  {
    root: { display: 'inline-block' },
  },
  { name: 'RaFileField' }
);

interface S3FileFieldProps extends FileFieldProps {
  onClick?: (e: MouseEvent) => void;
}

S3FileField.defaultProps = {
  addLabel: true,
};

// @ts-ignore
S3FileField.propTypes = {
  ...s3FileFieldType,
};

export { S3FileField };
